










































import fIcon from '@/components/m-icon/f-icon.vue'
import mInput from '../../components/m-input.vue'
import loginModal from '@/components/login-modal/login-modal.vue';
import {mapState, mapGetters, mapMutations} from 'vuex'

export default {
	components: {fIcon, mInput, loginModal},
	data() {
		return{
			showLogin:false,
			roles:[],
			list:[],
			mobile:''
		}
	},
	computed: {
		...mapState(['hasLogin', 'account']),
		...mapGetters(['agentMember']),
		isAdmin:function(){
			for(let i=0; i<this.roles.length; i++){
				if(this.roles[i].role == 'admin'){
					return true;
				}
			}
			return false;
		}
	},
	methods:{
		...mapMutations(['targetMember']),
		closeLoginModal:function(){
			this.showLogin = false;
		},
		enter:function(url){
			uni.navigateTo({
				url:url
			})
		},
		viewMember:function(member){
			this.targetMember(member);
			this.enter('/pages/member/detail');
		},
		check:function(member){
			let that = this;
			uni.showModal({
				title: '确定',
				content: '确定执行操作？',
				success: function (res) {
					if (res.confirm) {
						that.update(member);
					} 
				}
			});
		},
		update:function(member){
			let that = this;
			uni.request({
				url:this.$http.contextPath + 'base/member/updateById',
				method:'POST',
				data:{
					id:member.id,
					recommend:(member.recommend == '1')?'0':'1'
				},
				success(res) {
					if(that.$http.success(res)){
						member.recommend = (member.recommend == '1')?'0':'1';
					}
				}
			})
		},
		search:function(){
			// if(this.mobile == ''){
			// 	uni.showToast({
			// 		icon:'none',
			// 		title:'请输入手机号'
			// 	})
			// 	return;
			// }
			this.getMembers();
		},
		getAccountRole:function(){
			let that = this;
			uni.request({
				url:this.$http.contextPath + 'base/account-role/list',
				method:'POST',
				data:{
					status:'1',
					accountId:this.account.id
				},
				success(res) {
					if(that.$http.success(res)){
						that.roles = res.data.data;
					}
				}
			})
		},
		getMembers:function(){
			let that = this;
			let data = {
				status:'1'
			};
			if(this.mobile != ''){
				data.mobile = this.mobile;
			}else{
				data.recommend = "1";
			}
			uni.request({
				url:this.$http.contextPath + 'member/simpleQuery',
				method:'POST',
				data:data,
				success(res) {
					if(that.$http.success(res)){
						that.list = res.data.data;
					}
				}
			})
		}
	},
	onLoad() {
		if(this.hasLogin){
			this.getAccountRole();
			this.getMembers();
		}else{
			this.showLogin = true;
		}
	}
}
